/***/ (function(module, exports) {

	module.exports = {"data":{"site":{"siteMetadata":{"githubRepo":"https://github.com/esttorhe/esttorhe.github.io","author":"Esteban Torres"}},"markdownRemark":{"frontmatter":{"title":"How to (and not) ask questions…","issueNumber":22,"categories":["personal","learnings","personal improvement"],"tags":["questions","mistakes"],"author":null,"date":"2015-09-26T22:11:51.000Z"},"html":"<p>Is it safe to say that I've been «scolded» twice this week due to my technical questions.</p>\n<p>Now… I was not <em>scolded</em> as the word implies but the context was that; and I did deserve it if I'm honest.</p>\n<!--more-->\n<h1 id=\"strike-one\"><a href=\"#strike-one\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Strike One</h1>\n<p>First time was <a href=\"https://twitter.com/orta\">@Orta</a> on a Slack channel that was supposed to be just <code class=\"language-text\">#noise</code>.</p>\n<p>I was «abusing» the channel constantly asking questions regarding <code class=\"language-text\">CocoaPods</code>, either usage and/or for my <code class=\"language-text\">podspec</code>.</p>\n<p>Whilst I didn't do it with the idea of «abusing» it in mind but instead with the idea of maybe sharing some potentials issues (or not and avoid generating non-existent issues) and also thinking that since most of the creators/maintainers of the tool where on the channel I could get a better grip of what I was doing and avoid generating «noise» on the web I was doing it on a disorganized way.</p>\n<p>Even though my intentions were good my approach was not the correct.</p>\n<h1 id=\"strike-two\"><a href=\"#strike-two\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Strike Two</h1>\n<p>Tonight I felt short on my «questioning game» again:</p>\n<blockquote class=\"twitter-tweet\" lang=\"en\"><p lang=\"en\" dir=\"ltr\"><a href=\"https://twitter.com/esttorhe\">@esttorhe</a> Hey, why do you always make public shout-outs for questions? I don’t know, where/how do things get stuck?</p>&mdash; Felix Krause (@KrauseFx) <a href=\"https://twitter.com/KrauseFx/status/647975598216777728\">September 27, 2015</a></blockquote>\n<script async src=\"http://platform.twitter.com/widgets.js\" charset=\"utf-8\"></script>\n<p>Yet again; I don't think Felix was mad but he was on point. I was kind of shouting a question directly to him while the rest of the world stood there waiting.</p>\n<p>I was later «instructed» that opening with a mention would benefit by everyone who follow us both to read the thread.</p>\n<p>On my mind I thought about asking a private question but thought that sharing it with the world could be potentially benefitial to others (and maybe someone else could shed some light to the issue) but again I was bad on my «implementation».</p>\n<h1 id=\"so\"><a href=\"#so\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>So…</h1>\n<p>All in all I don't feel like they were «mad» or I was doing wrong; I believe I was approaching the issues from a bad angle.</p>\n<p>I always had in mind the idea of sharing knowledge and also avoiding noise; what was bad was the execution.</p>\n<p>This 2 events made me reflect deeply about how I am approaching my technical questions and what proper channels are already available to me.</p>\n<p>I know I'll always have this «other» channels but I shouldn't abuse them and instead actively participate in a smart way to make me an asset and not an ass.</p>\n<p>To sum up:</p>\n<ul>\n<li>Don't be afraid to ask questions.</li>\n<li>Don't be afraid to reach out to those who know more than you</li>\n<li>Be mindful of where you are asking the questions</li>\n<li>Be mindful of how you are asking said questions</li>\n</ul>\n<p>Just keeping those simple pointers will help you get far and would also avoid any potential embarrassments.</p>\n<p>After all I already made an ass of myself, the least you could do is learn from my mistakes.</p>","url":"How-to-and-not-ask-questions…"}},"pathContext":{"id":"/home/travis/build/esttorhe/esttorhe.github.io/content/_posts/2015-09-26-How-to-and-not-ask-questions….markdown absPath of file >>> MarkdownRemark"}}

/***/ })

